.basket {
    &-title {
        margin-bottom: 20px;
        width: 100%;
        left: 0;
        background-color: var(--White);
        font-size: 13px;
        color: var(--Charcoal);
        .container {
            display: flex;
            padding-right: 60px;
        }
        .basket__item-middlecell {
            &:last-child {
                width: 245px;
            }
        }
        span {
            display: inline-block;
        }
    }
    &__item {
        display: flex;
        align-items: center;
        padding: 20px 30px;
        margin-bottom: 20px;
        border: 1px solid var(--Gainsboro);
        font-size: 14px;
        transition: all 0.2s;
        &-minicell {
            width: 170px;
            flex-shrink: 0;
        }
        &-othercell {
            width: 270px;
            flex-shrink: 0;
        }
        &-middlecell {
            width: 230px;
            flex-shrink: 0;
        }
        p {
            margin: 0;
        }
        &-mobiletitle {
            display: none;
            font-size: 13px;
            font-weight: 400;
            color: var(--Charcoal);
            margin-bottom: 17px;
        }
        &:hover {
            box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.15);
        }
        &-main {
            display: flex;
            align-items: center;
            flex-grow: 1;
            flex-shrink: 1;
            &img {
                height: 135px;
                width: 240px;
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;
                flex-shrink: 0;
            }
            &info {
                margin-left: 30px;
                flex-shrink: 1;
                span {
                    font-size: 13px;
                }
                span:first-child {
                    margin-right: 30px;
                    color: var(--CodeColor);
                }
                span:last-child {
                    color: var(--PuertoRico);
                }
                &>a {
                    margin: 0;
                    margin-top: 10px;
                    line-height: 19px;
                    color: var(--Charcoal);
                    font-weight: 600;
                    padding-right: 120px;
                }
            }
        }
        &-btn {
            color: var(--Charcoal);
            flex-shrink: 0;
            transition: all 0.2s;
            &:hover {
                color: var(--Red);
            }
            i {
                font-size: 13px;
            }
        }
        .price {
            justify-content: flex-start;
            flex-wrap: wrap;
        }
        .count__wrap {
            width: 110px;
        }
        .count {
            margin: 0;
        }
        input {
            font-size: 13px;
        }
    }
    &-footer {
        padding: 40px 0 60px;
        margin: 0;
        justify-content: space-between;
    }
    &-pageaction {
        display: flex;
        align-items: center;
        align-self: flex-start;
        a {
            position: relative;
            display: flex;
            align-items: center;
            margin-right: 150px;
            i {
                position: absolute;
                transform: rotate(90deg);
                font-size: 6px;
            }
        }
        div {
            display: flex;
            align-items: center;
        }
        &__btn {
            display: flex;
            align-items: center;
            margin-left: 30px;
            color: var(--Charcoal);
            overflow: visible;
            i {
                margin-right: 10px;
                font-size: 12px;
                transition: all 0.2s;
            }
            span {
                border-bottom: 1px dashed var(--Charcoal);
            }
            &:hover {
                i {
                    color: var(--Red);
                }
            }
        }
    }
    &-back {
        display: inline-block;
        margin-left: 20px;
        &:after {
            content: "";
            display: block;
            width: 100%;
            height: 1px;
            background-color: var(--mainColor);
            transition: all 0.2s;
        }
        &:hover {
            &:after {
                background-color: var(--White);
            }
        }
    }
    &-total {
        display: flex;
        &__price-item {
            display: flex;
            &:first-child {
                font-size: 20px;
                font-weight: 700;
            }
            &:not(:last-child) {
                margin-bottom: 10px;
            }
            p {
                margin: 0;
                &:first-child {
                    width: 110px;
                    margin-right: 14px;
                }
            }
        }
        &__btn {
            margin-left: 58px;
            button {
                padding: 8px 36px;
            }
        }
        .Btn-Flamenco {
            background-color: var(--Wedgewood);
            border-color: var(--Wedgewood);
            margin-left: 6px;
            &:hover{
                background-color: var(--Flamenco);
                border-color: var(--Flamenco);
            }
        }
    }
}
@media(max-width: 1650px) {
    .basket__item-maininfo>a {
        padding-right: 50px;
    }
}
@media(max-width: 1550px) {
    .basket {
        &__item-minicell {
            width: 100px;
        }
        &__item-middlecell {
            width: 165px;
        }
        &__item-othercell {
            width: 170px;
        }
        &-title .basket__item-middlecell:last-child {
            width: 178px;
        }
    }
}
@media(max-width: 1250px) {
    .basket {
        &-title {
            .basket__item-middlecell {
                &:last-child {
                    width: 225px;
                }
            }
        }
        &__item {
            &-minicell {
                width: 150px;
            }
            &-middlecell {
                width: 210px;
            }
            &-othercell {
                width: 250px;
            }
            &-maininfo {
                p {
                    padding-right: 30px;
                }
            }
        }
    }
}
@media(max-width: 1250px) {
    .basket {
        &-title {
            display: none;
        }
        &__item {
            position: relative;
            padding: 30px 20px 20px;
            margin-bottom: 10px;
            flex-wrap: wrap;
            &-main {
                width: 100%;
                margin-bottom: 28px;
                &info {
                    margin-left: 10px;
                }
            }
            &-mobiletitle {
                display: block;
            }
            &-btn {
                position: absolute;
                top: 20px;
                right: 20px;
            }
            &>div:not(.basket__item-main) {
                max-width: 25%;
                width: 100%;
                align-self: flex-start;
            }
            &-minicell {
                padding-left: 45px;
            }
            &-middlecell:last-of-type {
                padding-right: 45px;
            }
        }
    }
}
@media(max-width: 1350px) {
    .basket {
        &-pageaction {
            a {
                margin-right: 50px;
            }
        }
    }
}
@media(max-width: 1250px) {
    .basket {
        &-pageaction {
            width: 100%;
            justify-content: space-between;
        }
        &-total {
            width: 100%;
            justify-content: space-between;
            margin-top: 25px;
        }
    }
}
@media(max-width: 950px) {
    .basket {
        &__item {
            padding: 20px;
            &-main {
                &img {
                    width: 146px;
                    height: 82px;
                }
            }
            .price {
                span {
                    display: inline-block;
                    width: 100%;
                }
                del {
                    display: inline-block;
                    margin-left: 0;
                    margin-top: 8px;
                }
            }
            &-mobiletitle {
                margin-bottom: 10px;
            }
            &>div:nth-of-type(3) {
                max-width: 30%;
            }
            &>div:nth-of-type(5) {
                max-width: 20%;
            }
        }
    }
}
@media(max-width: 767px) {
    .basket {
        &-footer {
            padding: 20px 0 30px;
            margin: 0;
        }
        &__item-maininfo>a {
            margin-top: 4px;
            font-size: 14px;
        }
        &__item-btn {
            top: 15px;
        }
        &__item-btn i {
            font-size: 12px;
        }
    }
}
@media(max-width: 700px) {
    .basket {
        &__item {
            &-minicell {
                padding-left: 0;
            }
            &-middlecell:last-of-type {
                padding-right: 0;
            }
            &>div:not(.basket__item-main) {
                max-width: 50%;
                width: 100%;
                margin-top: 20px;
            }
            &-main {
                margin-bottom: 0;
            }
        }
    }
}
@media(max-width: 700px) {
    .basket {
        &-pageaction {
            flex-wrap: wrap;
            div {
                width: 100%;
                margin-top: 20px;
            }
        }
        &-total {
            flex-wrap: wrap;
            div {
                width: 100%;
            }
            &__btn {
                display: flex;
                margin-top: 30px;
                margin-left: 0;
                button {
                    flex-grow: 1;
                }
            }
        }
    }
}
@media(max-width: 575px) {
    .basket {
        &__item {
            &-main {
                flex-wrap: wrap;
                &info {
                    width: 100%;
                    margin-top: 20px;
                    margin-left: 0;
                    &>a {
                        padding-right: 0;
                    }
                }
            }
        }
        &-footer {
            padding-top: 10px;
        }
    }
}
@media(max-width: 400px) {
    .basket {
        &-pageaction {
            div {
                flex-wrap: wrap;
            }
            &__btn {
                margin-left: 0;
                width: 100%;
                margin-top: 10px;
            }
        }
        &-total {
            &__btn {
                flex-wrap: wrap;
                .Btn-Flamenco {
                    margin-left: 0;
                }
                button {
                    width: 100%;
                    &:last-child {
                        margin-top: 10px;
                    }
                }
            }
        }
    }
}

